.Sidenav {
  height: 100%;
  width: 9%;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  /*background-color: #474747;*/
  ;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.Navbar-Buttons {
  max-width: 60%;
  max-height: 80%;
  border-radius: 5px;

}


.Navbar-Buttons {
  width: 100%;
  fill: green;
}

.Navbar-Buttons :hover {
  background-color: rgba(255, 255, 255, 0.1);
  cursor: pointer;
}

.Navbar-Button-Images {
  width: 80%;
  max-width: 80%;
  max-height: 80%;
  fill: green
}